<template>
  <div class="min-h-screen flex items-center justify-center bg-gradient-to-br from-[#0f172a] via-[#1e293b] to-[#0f172a] px-4">
    <!-- TOAST -->
    <transition
      enter-active-class="animate-in slide-in-from-top fade-in duration-300"
      leave-active-class="animate-out slide-out-to-top fade-out duration-300"
    >
      <div
        v-if="toast.message"
        :class="[
          'fixed top-6 left-1/2 transform -translate-x-1/2 px-6 py-3 rounded-lg shadow-lg text-sm font-medium z-50',
          toast.type === 'success' ? 'bg-green-600 text-white' : 'bg-red-600 text-white'
        ]"
      >
        {{ toast.message }}
      </div>
    </transition>

    <!-- LOGIN CARD -->
    <div class="w-full max-w-md bg-gradient-to-br from-white/80 to-blue-100/90 p-10 rounded-3xl shadow-xl border border-blue-100 animate-in fade-in zoom-in-95 duration-500">
      <div class="text-center mb-8">
        <h2 class="text-3xl font-extrabold text-blue-700 tracking-tight drop-shadow">Welcome Back</h2>
        <p class="text-sm text-blue-700/60 mt-1">Sign in to continue to your dashboard</p>
      </div>

      <form class="space-y-7" @submit.prevent="submit" autocomplete="on">
        <div>
          <label for="loginInput" class="block text-blue-900 font-medium mb-2">Username or Email</label>
          <div class="relative">
            <span class="absolute left-3 top-3 text-blue-300">
              <svg width="20" height="20" fill="currentColor"><path d="M10 10a4 4 0 1 0 0-8 4 4 0 0 0 0 8Zm0 2c-2.98 0-9 1.52-9 4.5V19a1 1 0 0 0 1 1h16a1 1 0 0 0 1-1v-2.5c0-2.98-6.02-4.5-9-4.5Z"></path></svg>
            </span>
            <input
              id="loginInput"
              required
              name="email"
              type="text"
              placeholder="Type username or email"
              autofocus
              class="pl-10 w-full bg-blue-50 border border-blue-200 text-blue-900 placeholder-blue-300 focus:ring-2 focus:ring-blue-300 focus:outline-none rounded-lg px-4 py-3 transition shadow"
              autocomplete="username"
            />
          </div>
        </div>

        <div>
          <label for="passwordInput" class="block text-blue-900 font-medium mb-2">Password</label>
          <div class="relative">
            <span class="absolute left-3 top-3 text-blue-300">
              <svg width="20" height="20" fill="currentColor"><path d="M10 12a5 5 0 0 0-5 5v1a1 1 0 0 0 1 1h8a1 1 0 0 0 1-1v-1a5 5 0 0 0-5-5Zm0-2a3 3 0 1 0 0-6 3 3 0 0 0 0 6Z"/></svg>
            </span>
            <input
              :type="showPassword ? 'text' : 'password'"
              id="passwordInput"
              required
              name="password"
              placeholder="Password"
              class="pl-10 w-full bg-blue-50 border border-blue-200 text-blue-900 placeholder-blue-300 focus:ring-2 focus:ring-blue-300 focus:outline-none rounded-lg px-4 py-3 transition shadow"
              autocomplete="current-password"
            />
            <button
              type="button"
              tabindex="-1"
              class="absolute right-4 top-3 text-blue-400"
              @click="showPassword = !showPassword"
              :aria-label="showPassword ? 'Hide password' : 'Show password'"
            >
              <svg v-if="!showPassword" width="20" height="20" fill="none" stroke="currentColor"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M2.06 10.94C3.4 8.03 6.88 5.5 10 5.5s6.6 2.53 7.94 5.44a1 1 0 0 1 0 .12c-1.3 2.84-4.74 5.44-7.94 5.44s-6.64-2.6-7.94-5.44a1 1 0 0 1 0-.12z"></path><circle cx="10" cy="10" r="2" /></svg>
              <svg v-else width="20" height="20" fill="none" stroke="currentColor"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M13.875 13.875A3 3 0 0 1 10 13a3 3 0 0 1-3-3m5.208-5.208A7.43 7.43 0 0 1 10 4.5c-3.12 0-6.6 2.53-7.94 5.44a1 1 0 0 0 0 .12c1.3 2.84 4.74 5.44 7.94 5.44 1.568 0 3.031-.44 4.25-1.187"/><line x1="3" y1="3" x2="17" y2="17" stroke="currentColor" stroke-width="2"/></svg>
            </button>
          </div>
        </div>

        <div class="flex items-center justify-between text-xs text-blue-800 mb-1">
          <label class="flex items-center gap-2 cursor-pointer font-medium">
            <input type="checkbox" class="form-checkbox text-blue-600 bg-blue-50 border-blue-200 rounded transition" />
            Remember me
          </label>
          <a href="#" class="text-blue-500 hover:underline">Forgot password?</a>
        </div>

        <Button
          :loading="session.login.loading"
          :disabled="session.login.loading"
          variant="solid"
          class="w-full bg-gradient-to-r from-blue-500 to-teal-400 text-white font-semibold py-3 rounded-xl shadow-md hover:shadow-lg transition hover:opacity-90"
        >
          <template v-if="!session.login.loading">Login</template>
          <template v-else>Logging in...</template>
        </Button>
      </form>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import { session } from '../data/session'

const router = useRouter()
const toast = ref({
  message: '',
  type: 'success', // 'success' or 'error'
})
const showPassword = ref(false)

function showToast(message: string, type: 'success' | 'error' = 'success') {
  toast.value = { message, type }
  setTimeout(() => {
    toast.value.message = ''
  }, 3000)
}

async function submit(e: Event) {
  const formData = new FormData(e.target as HTMLFormElement)
  const email = formData.get('email') as string
  const password = formData.get('password') as string

  try {
    await session.login.submit({ email, password })
    showToast('Login successful!', 'success')
    router.push('/dashboard')
  } catch (error) {
    showToast('Login failed. Please check your credentials.', 'error')
  }
}
</script>
